/**
 * Initialize the preflight check UI.
 */
export const init = () => {
    const ids = ['fgroup_id_buttonar', 'id_submitbutton', 'id_cancel'];
    ids.forEach(id => {
        const element = document.getElementById(id);
        if (element) {
            element.style.display = 'none';
        }
    });
};
